/**
 * è·å– url å‚æ•°
 * @param {*} variable ä¼ å…¥ key
 * @returns æœ‰å€¼å°±è¿”å›å€¼ï¼Œæ²¡æœ‰å°±è¿”å› false
 */
const getQueryVariable = (variable) => {
  let query = window.location.search.substring(1);
  let vars = query.split("&");
  for (let i = 0; i < vars.length; i++) {
    let pair = vars[i].split("=");
    if (pair[0] == variable) {
      return decodeURI(pair[1]);
    }
  }
  return false;
};
/**
 * å°† url åœ°å€ä¸­çš„ t å‚æ•°æ”¾å…¥æ–‡å­—æ˜¾ç¤ºåŒºåŸŸ
 */
((text) => {
  let t = getQueryVariable("t");
  if (t) {
    text.innerHTML = t;
  }
})(document.querySelector("#text"));

/**
 * ä¸‹é¢å…¨æ˜¯ çˆ±å¿ƒä»£ç éƒ¨åˆ†
 */
const settings = {
  length: 5200, // æœ€å¤§é¢—ç²’æ•°é‡
  duration: 4, // ç²’å­æŒç»­æ—¶é—´
  velocity: 80, // ç²’å­é€Ÿåº¦
  effect: -1.3, // è¶…å‡ºéƒ¨åˆ†
  size: 8, // ç²’å­å¤§å° ï¼ˆåƒç´ ï¼‰
  color: "#ee3e3e", // é¢œè‰²
};

/**
 * Point
 */
const Point = (() => {
  function Point(x, y) {
    this.x = typeof x !== "undefined" ? x : 0;
    this.y = typeof y !== "undefined" ? y : 0;
  }

  Point.prototype.clone = function () {
    return new Point(this.x, this.y);
  };

  Point.prototype.length = function (length) {
    if (typeof length == "undefined") {
      return Math.sqrt(this.x * this.x + this.y * this.y);
    }
    this.normalize();
    this.x *= length;
    this.y *= length;
    return this;
  };

  Point.prototype.normalize = function () {
    let length = this.length();
    this.x /= length;
    this.y /= length;
    return this;
  };

  return Point;
})();

/**
 * Particle ç²’å­
 */
const Particle = (() => {
  function Particle() {
    this.position = new Point();
    this.velocity = new Point();
    this.acceleration = new Point();
    this.age = 0;
  }

  Particle.prototype.initialize = function (x, y, dx, dy) {
    this.position.x = x;
    this.position.y = y;
    this.velocity.x = dx;
    this.velocity.y = dy;
    this.acceleration.x = dx * settings.effect;
    this.acceleration.y = dy * settings.effect;
    this.age = 0;
  };

  Particle.prototype.update = function (deltaTime) {
    this.position.x += this.velocity.x * deltaTime;
    this.position.y += this.velocity.y * deltaTime;
    this.velocity.x += this.acceleration.x * deltaTime;
    this.velocity.y += this.acceleration.y * deltaTime;
    this.age += deltaTime;
  };

  Particle.prototype.draw = function (context, image) {
    function ease(t) {
      return --t * t * t + 1;
    }
    let size = image.width * ease(this.age / settings.duration);
    context.globalAlpha = 1 - this.age / settings.duration;
    context.drawImage(
      image,
      this.position.x - size / 2,
      this.position.y - size / 2,
      size,
      size
    );
  };

  return Particle;
})();

/**
 * ParticlePool ç²’å­æ± 
 */
const ParticlePool = (() => {
  let particles,
    firstActive = 0,
    firstFree = 0,
    duration = settings.duration;

  function ParticlePool(length) {
    // åˆ›å»ºå’Œå¡«å……ç²’å­æ± 
    particles = new Array(length);
    for (var i = 0; i < particles.length; i++) particles[i] = new Particle();
  }

  ParticlePool.prototype.add = function (x, y, dx, dy) {
    particles[firstFree].initialize(x, y, dx, dy);

    // å¤„ç†å¾ªç¯é˜Ÿåˆ—
    firstFree++;
    if (firstFree == particles.length) firstFree = 0;
    if (firstActive == firstFree) firstActive++;
    if (firstActive == particles.length) firstActive = 0;
  };

  ParticlePool.prototype.update = function (deltaTime) {
    let i;

    // æ›´æ–°æ´»åŠ¨ç²’å­
    if (firstActive < firstFree) {
      for (i = firstActive; i < firstFree; i++) particles[i].update(deltaTime);
    }
    if (firstFree < firstActive) {
      for (i = firstActive; i < particles.length; i++)
        particles[i].update(deltaTime);
      for (i = 0; i < firstFree; i++) particles[i].update(deltaTime);
    }

    // åˆ é™¤ä¸æ´»è·ƒçš„ç²’å­
    while (particles[firstActive].age >= duration && firstActive != firstFree) {
      firstActive++;
      if (firstActive == particles.length) firstActive = 0;
    }
  };

  ParticlePool.prototype.draw = function (context, image) {
    // ç”»å‡ºæ´»åŠ¨ç²’å­
    if (firstActive < firstFree) {
      for (var i = firstActive; i < firstFree; i++)
        particles[i].draw(context, image);
    }
    if (firstFree < firstActive) {
      for (var i = firstActive; i < particles.length; i++)
        particles[i].draw(context, image);
      for (var i = 0; i < firstFree; i++) particles[i].draw(context, image);
    }
  };

  return ParticlePool;
})();

export {
  Point,
  Particle,
  ParticlePool,
  settings
}
